import React, { useState, useRef, useMemo } from "react";
import { useNavigate } from "react-router-dom";
import { Breadcrumb, Typography, Row, Button, Form, Table } from "antd";
import withAuth from "../../HOC/withAuth";
import { Card, FormInput, GridLarge, GridSmall } from "../../Components";
import { FiPlusSquare, FiSearch } from "react-icons/fi";
import { inActiveColor } from "../../Utils/colors";
import { useGetData, useDisclosure } from "../../Hooks";
import debounce from "lodash.debounce";
import ModalCreateUpdateArea from "../../Components/UserManagement/ModalCreateUpdateArea";

const { Title, Text } = Typography;

const Area = () => {
  const [updateData, setUpdateData] = useState(0);
  const { data, loading } = useGetData("area", updateData);

  const [searchData, setSearchData] = useState("");
  const [dataTemp, setdataTemp] = useState({});
  const [updating, setUpdating] = useState(false);

  const newArea = useMemo(
    () =>
      data.length === 0
        ? []
        : data.filter((item) =>
            item.name?.toLowerCase().includes(searchData?.toLowerCase())
          ),
    [data, searchData]
  );

  const { isOpen, onOpen, onClose } = useDisclosure();
  const navigate = useNavigate();

  const handleSearchData = useRef(
    debounce((valueSearch) => setSearchData(valueSearch), 1000)
  ).current;

  const handleNewArea = () => {
    setUpdating(false);
    onOpen();
  };

  const handelUpdateArea = (record) => {
    setUpdating(true);
    setdataTemp(record);
    onOpen();
  };

  const columns = [
    {
      title: "#",
      dataIndex: "id",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => handelUpdateArea(record)}
        >
          {text}
        </Text>
      ),
      width: 70,
    },
    {
      title: "Area Id",
      dataIndex: "area",
      render: (text, record) => (
        <Text
          strong
          style={{ cursor: "pointer" }}
          onClick={() => handelUpdateArea(record)}
        >
          {text}
        </Text>
      ),
    },
    {
      title: "Area Name",
      dataIndex: "name",
    },
  ];

  return (
    <div>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item
          style={{ cursor: "pointer" }}
          onClick={() => navigate("/user-management")}
        >
          User Management
        </Breadcrumb.Item>
        <Breadcrumb.Item>Area</Breadcrumb.Item>
      </Breadcrumb>
      <ModalCreateUpdateArea
        visible={isOpen}
        onCancel={onClose}
        setUpdateData={setUpdateData}
        data={dataTemp}
        updating={updating}
      />
      <div>
        <Card>
          <Title
            level={3}
            type="secondary"
            style={{ marginRight: 20, marginBottom: 30 }}
          >
            Area
          </Title>
          <Row gutter={[16, 10]}>
            <GridLarge>
              <Button
                shape="round"
                icon={<FiPlusSquare style={{ marginRight: 6 }} />}
                onClick={handleNewArea}
              >
                New Area
              </Button>
            </GridLarge>
            <GridSmall>
              <Form name="searchArea" autoComplete="off">
                <FormInput
                  name="search"
                  prefix={<FiSearch color={inActiveColor} />}
                  allowClear={true}
                  placeholder="serach area"
                  value={searchData}
                  onChange={(val) => handleSearchData(val.target.value)}
                />
              </Form>
            </GridSmall>
          </Row>
          <div style={{ marginTop: "20px" }}>
            <Table
              columns={columns}
              dataSource={newArea}
              rowKey={(record) => record.id}
              loading={loading}
              scroll={{ y: 500 }}
            />
          </div>
        </Card>
      </div>
    </div>
  );
};

export default withAuth(Area);
