import { useContext, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../Store/AuthContext";
const withAuth = (WrappedComponent, protectedPage) => {
  // eslint-disable-next-line react/display-name
  return (props) => {
    const authContext = useContext(AuthContext);
    const navigate = useNavigate();
    const accessToken = authContext.userToken;

    useEffect(() => {
      if (typeof window !== "undefined") {
        if (!accessToken) {
          navigate("/auth", { replace: true });
        } else if (protectedPage?.indexOf(authContext.userRole) === -1) {
          navigate("/403", { replace: true });
        }
      }
    }, [accessToken, authContext.userRole, navigate]);

    if (typeof window !== "undefined") {
      if (authContext.isLoading) {
        console.log("loading from with auth");
        return null;
      }

      return <WrappedComponent {...props} />;
    }
    return null;
  };
};

export default withAuth;
