import moment from "moment";
import debounce from "lodash.debounce";

const myNumberFormat = (number) => {
  return new Intl.NumberFormat("id-ID").format(number);
};

const myCurrencyFormat = (curr) => {
  return new Intl.NumberFormat("id-ID", {
    style: "currency",
    currency: "IDR",
  }).format(curr);
};

const myDateTimeFormat = (date) => {
  return new Date(date).toLocaleString("id-ID", {
    weekday: "short",
    year: "numeric",
    month: "short",
    day: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  });
};

const myDateFormat = (date) => {
  return new Date(date).toLocaleDateString("id-ID", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

const myTimeFormat = (time) => {
  return new Date(time).toLocaleTimeString("id-ID", {
    hour: "2-digit",
    minute: "2-digit",
  });
};

const myDateIndo = (date) => {
  if (typeof date === "object") {
    const newDate = moment(date).format();

    return newDate.substring(0, 10);
  }
  return date;
};

const mySum = (arr) => {
  if (typeof arr === "object") {
    const reduce = arr.reduce((acc, curr) => acc + curr);
    return reduce;
  }
  return arr;
};

const toCapitalize = (string) => {
  if (typeof string === "string") {
    return string.replace(/\w\S*/g, (w) =>
      w.replace(/^\w/, (c) => c.toUpperCase())
    );
  }
  return string;
};

const setInput = debounce((e, setValue) => setValue(e.target.value), 500);

const getChartOverview = (prev, arr, selectFilter, selectMonth) => {
  if (arr.length === 0) {
    return [];
  }

  const tahun = new Date().getFullYear();
  const bulan = new Date().getMonth() + 1;
  const sliceData = arr[0].iyear === tahun ? bulan + 1 : 13;
  let prevData = prev;
  let jan = 0;
  let feb = 0;
  let mar = 0;
  let apr = 0;
  let mei = 0;
  let jun = 0;
  let jul = 0;
  let agu = 0;
  let sep = 0;
  let okt = 0;
  let nov = 0;
  let des = 0;

  const dataFilter = arr.map((item) => item[selectFilter]);

  arr.map((item) => {
    if (item[selectMonth] === 1) {
      jan = dataFilter[0] + prev;
    }
    if (item[selectMonth] === 2) {
      feb = mySum(dataFilter.slice(0, 2)) + prev;
    }
    if (item[selectMonth] === 3) {
      mar = mySum(dataFilter.slice(0, 3)) + prev;
    }
    if (item[selectMonth] === 4) {
      apr = mySum(dataFilter.slice(0, 4)) + prev;
    }
    if (item[selectMonth] === 5) {
      mei = mySum(dataFilter.slice(0, 5)) + prev;
    }
    if (item[selectMonth] === 6) {
      jun = mySum(dataFilter.slice(0, 6)) + prev;
    }
    if (item[selectMonth] === 7) {
      jul = mySum(dataFilter.slice(0, 7)) + prev;
    }
    if (item[selectMonth] === 8) {
      agu = mySum(dataFilter.slice(0, 8)) + prev;
    }
    if (item[selectMonth] === 9) {
      sep = mySum(dataFilter.slice(0, 9)) + prev;
    }
    if (item[selectMonth] === 10) {
      okt = mySum(dataFilter.slice(0, 10)) + prev;
    }
    if (item[selectMonth] === 11) {
      nov = mySum(dataFilter.slice(0, 11)) + prev;
    }
    if (item[selectMonth] === 12) {
      des = mySum(dataFilter.slice(0, 12)) + prev;
    }

    return item[selectMonth];
  });

  return [
    prevData,
    jan,
    feb,
    mar,
    apr,
    mei,
    jun,
    jul,
    agu,
    sep,
    okt,
    nov,
    des,
  ].slice(0, sliceData);
};

const getRandomColor = () => {
  return Math.floor(Math.random() * 9);
};

const myMeterFormat = number => {
  if (typeof number === 'number') {
    if (number >= 1000000) {
      return `${(number / 1000000).toFixed(2)} MM`;
    } else if (number >= 1000) {
      return `${(number / 1000).toFixed(2)} KM`;
    } else {
      return `${number} Meter`;
    }
  }

  return number;
};

export {
  myNumberFormat,
  myCurrencyFormat,
  myDateTimeFormat,
  myDateFormat,
  myTimeFormat,
  mySum,
  toCapitalize,
  myDateIndo,
  setInput,
  getChartOverview,
  getRandomColor,
  myMeterFormat,
};
