import React, { useState, useEffect } from "react";
import { Modal, Form, Divider, Button, message, Select } from "antd";
import { FormInput, FlexEnd, FormSelect } from "..";
import { usePostData, useUpdateData } from "../../Hooks";
import { setInput } from "../../Utils/myFormat";
import { FiNavigation } from "react-icons/fi";

const { Option } = Select;

const ModalCreateUpdateArea = ({
  visible,
  onCancel,
  setUpdateData,
  data,
  updating,
}) => {
  const [areaNo, setAreaNo] = useState("");
  const [areaId, setAreaId] = useState("");
  const [areaName, setAreaName] = useState("");
  const [status, setStatus] = useState("");

  const resetState = () => {
    setAreaNo("");
    setAreaId("");
    setAreaName("");
    setStatus("");
    form.resetFields();
  };

  const [form] = Form.useForm();

  const [dataPost, setDataPost] = useState({
    url: "",
    data: {},
    trigger: 0,
  });
  const [dataUpdate, setDataUpdate] = useState({
    url: "",
    data: {},
    trigger: 0,
  });

  const { onSuccess: successPost, loading: loadingPost } = usePostData(
    dataPost.url,
    dataPost.data,
    dataPost.trigger
  );
  const { onSuccess: successUpdate, loading: loadingUpdate } = useUpdateData(
    dataUpdate.url,
    dataUpdate.data,
    dataUpdate.trigger
  );

  const handlePostArea = () => {
    if (!areaId || !areaName || status === "") {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    const dataPost = {
      area: areaId,
      name: areaName,
      status: status,
    };
    console.log("data post: ", dataPost);

    setDataPost({
      url: "area/add",
      data: dataPost,
      trigger: new Date().getTime(),
    });
  };

  const handleUpdateArea = () => {
    if (!areaNo || !areaId || !areaName || status === "") {
      message.warn("Form tidak boleh kosong");

      return null;
    }

    const dataUpdate = {
      area_id: areaNo,
      area: areaId,
      status: status,
      name: areaName,
    };
    console.log("data update: ", dataUpdate);

    setDataUpdate({
      url: "area/edit",
      data: dataUpdate,
      trigger: new Date().getTime(),
    });
  };

  useEffect(() => {
    if (successPost.includes("success")) {
      message.success(`Berhasil Create Area ${areaId}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }
    if (successUpdate.includes("success")) {
      message.success(`Berhasil Update Area ${areaId}`);
      resetState();
      onCancel();
      setUpdateData(Date.now());
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [successPost, successUpdate]);

  useEffect(() => {
    if (data.id && updating) {
      setAreaNo(data.id);
      setAreaId(data.area);
      setAreaName(data.name);
      form.setFieldsValue({
        areaNo: data.id,
        areaId: data.area,
        areaName: data.name,
      });
    } else {
      resetState();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data, updating]);

  return (
    <Modal
      title={updating ? "Update Area" : "Create Area"}
      visible={visible}
      onCancel={onCancel}
      footer={false}
    >
      <Form form={form} name="createsite" autoComplete="off">
        <FormInput
          label="Area Id"
          requiredLabel
          name="areaId"
          required
          message="please input area id!"
          placeholder="input area id"
          onChange={(e) => setInput(e, setAreaId)}
          disabled={updating ? true : false}
        />
        <FormInput
          label="Area Name"
          requiredLabel
          name="areaName"
          required
          message="please input area name!"
          placeholder="input area name"
          onChange={(e) => setInput(e, setAreaName)}
        />
        <FormSelect
          name="status"
          label="Status"
          requiredLabel
          required
          message="Please select status!"
          placeholder="select status"
          onChange={(e) => setStatus(e)}
        >
          <Option value={1}>Active</Option>
          <Option value={0}>Inactive</Option>
        </FormSelect>
        <Divider />
        <FlexEnd>
          <Form.Item>
            {updating ? (
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation style={{ marginRight: "6px" }} />}
                onClick={handleUpdateArea}
                loading={loadingUpdate}
                htmlType="submit"
              >
                Update Area
              </Button>
            ) : (
              <Button
                type="primary"
                shape="round"
                icon={<FiNavigation style={{ marginRight: "6px" }} />}
                onClick={handlePostArea}
                loading={loadingPost}
                htmlType="submit"
              >
                Create Area
              </Button>
            )}
          </Form.Item>
        </FlexEnd>
      </Form>
    </Modal>
  );
};

export default ModalCreateUpdateArea;
